import React, { Component } from 'react'
import Todoing from './Todoing';
import Todoinput from './Todoinput';
import { callbackify } from 'util';

export default class Todolist extends Component {
    constructor(){
        super();
        this.state = {
            //完成加载后以及加载之前
            todo:JSON.parse(localStorage.getItem('one') || '[]'),//正在进行
            todo2:JSON.parse(localStorage.getItem('two') || '[]')//已经完成
        }
    }
    addItem = (msg)=>{
        this.setState({
            todo: [...this.state.todo,msg],
        },()=>{
            localStorage.setItem("one",JSON.stringify(this.state.todo)); 
        })
    }
    delItem = (item)=>{
        var todo = [...this.state.todo];
        todo.splice(item,1);
        this.setState({
            todo:todo
        },()=>{
            localStorage.setItem("one",JSON.stringify(this.state.todo)); 
        })
    }

    delItem2 = (item)=>{
        var todo2 = [...this.state.todo2];
        todo2.splice(item,1);
        this.setState({
            todo2:todo2
        },()=>{
            localStorage.setItem("two",JSON.stringify(this.state.todo2)); 
        })
    }

    changeItem1 =(item)=>{
        var todo = [...this.state.todo];
        this.delItem(item);
        this.setState({
            todo2: [...this.state.todo2,todo[item]],
        },()=>{
            localStorage.setItem('two',JSON.stringify(this.state.todo2)); 
        })
    }

    changeItem2 =(item)=>{
        var todo2 = [...this.state.todo2];
        this.delItem2(item);
        this.setState({
            todo: [...this.state.todo,todo2[item]],
        },()=>{
            localStorage.setItem('one',JSON.stringify(this.state.todo)); 
        })
    }

    render() {
        return (
            <div>
                <Todoinput addTodo={this.addItem} />
                <Todoing delTodo={this.delItem} delTodo2={this.delItem2} todo={this.state.todo} todo2={this.state.todo2} changeItem1={this.changeItem1} changeItem2={this.changeItem2}/>
            </div>
        )
    }
}