import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { prototype } from 'events'
import Todolist from './Todolist'
export default class Todoing extends Component {
    render() {
        return (
            <div>
                {/* 正在进行 */}
                <div style={{fontSize:20,float:"left"}}>正在进行</div>
                {/* 计数 */}
                <div style={{float:"left",fontSize:20}}>{this.props.todo.length}</div>
                <br/><br/>
                <ul className="list">
                    {
                        this.props.todo.map((item,idx)=><li key={item}><input type="checkbox" id="checkbox" onClick={()=>{this.props.changeItem1(idx)}} />----{item}----<button onClick={()=>{this.props.delTodo(idx)}}>删除</button></li>)
                    }
                </ul>
                {/* 已经完成 */}
                <div style={{fontSize:20,float:"left"}}>已经完成</div>
                {/* 计数 */}
                <div style={{float:"left",fontSize:20}}>{this.props.todo2.length}</div>
                <br/><br/>
                <ul className="list">
                    {
                        this.props.todo2.map((item,idx)=><li key={item}><input type="checkbox" checked="checked" readOnly={true} onClick={()=>{this.props.changeItem2(idx)}} />----{item}----<button onClick={()=>{this.props.delTodo2(idx)}}>删除</button></li>)
                    }
                </ul>
            </div>
        )
    }
}
Todoing.propTypes = {
    todo: PropTypes.array,
    todo2: PropTypes.array
}